import requestHttps from '../../../common/request.js'
export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			ordnavData:['全部','待付款','待发货','待收货','已完成'],
			curent:0,
			type:1,
			page:1,
			typeid:0,
			orderList:[],
			flag:true,
			medix:true,
			prolodding:2,
			liloading:true
		}
	},
	methods: {
		tabnav:function(e){
			var that=this
			var cur=e.currentTarget.dataset.curent
			that.curent=cur 
			that.page=1
			that.orderList=[]
			that.flag=true
			that.getordlist()
		},
		// 返回上个页面
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		},
		getordlist:function(){
			var that=this
			if(that.flag){
				requestHttps({
					url:'/order/lists',
					method:'POST',
					data:{
						page:that.page,
						status:that.type
					},
					success:res=>{
						console.log(res)
						var data=res.data
						var list=data.data
						 var lood=2 
						if(that.page==1){
							that.orderList=list
							
						}else{
							that.orderList=that.orderList.concat(list)
						}
						if (list != '') {
						      lood = 1
						  } else {
						      lood = 3
						  }
						  if(list==''&& that.page>1){
							  lood = 1
						  }
						that.prolodding=lood
						if(list.length>=1 ){
							that.flag=true
							that.medix=true 
							that.liloading=false
						}else{
							that.liloading=false
							that.flag=false
							that.medix=false 
							
						}
						 
					}
				})
			}
			
		},
	},
	onLoad:function(options){
		var that=this
		var type=options.type
		var title=options.title 
		console.log(options)
		that.type=type 
		that.curent=type
		uni.setNavigationBarTitle({
			title:title
		})
		that.getordlist()
		 
	},
	onReachBottom:function(){
		var that=this
		if(that.flag){
			var page=that.page
			page++
			that.page=page
			that.getordlist()
		}
	}
}
